.page__section {

    width: 100%
}

.page__header {

    width: 100%;

    height: 5vh
}

.page__main {

    overflow: hidden;

    --tw-perspective: 1000px;

    perspective: var(--tw-perspective);

    height: 90vh
}

.main__layers-container {

    height: 100%;

    transition-property: perspective, translate, scale, transform, perspective, rotate;

    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    transition-duration: 150ms;

    will-change: transform;

    transform-style: preserve-3d
}

.main__layer {

    position: absolute;

    height: 100%;

    width: 100%
}

.layer__wallpaper {

    inset: -9rem;

    background-image: url('src/assets/pictures/city.wallapaper.jpg');

    background-size: cover;

    background-position: center
}

.layer__logotype {

    inset: 0px;

    display: flex;

    align-items: center;

    justify-content: center;

    background-size: cover;

    background-position: center;

    --tw-rotate-y: 2deg;

    --webkit-transform: rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y));

    transform: rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y))
}

.layer__console {

    inset: 0px;

    display: flex;

    flex-direction: column;

    align-items: center;

    justify-content: center
}
